:root {
  --global-font-size: 15px;
  --global-line-height: 1.4em;
  --global-space: 10px;
  --font-stack: Menlo, Monaco, Lucida Console, Liberation Mono, DejaVu Sans Mono, Bitstream Vera Sans Mono, Courier New, monospace, serif;
  --mono-font-stack: Menlo, Monaco, Lucida Console, Liberation Mono, DejaVu Sans Mono, Bitstream Vera Sans Mono, Courier New, monospace, serif;
  --background-color: #fff;
  --page-width: 60em;
  --font-color: #151515;
  --invert-font-color: #fff;
  --primary-color: #1a95e0;
  --secondary-color: #727578;
  --error-color: #d20962;
  --progress-bar-background: #727578;
  --progress-bar-fill: #151515;
  --code-bg-color: #e8eff2;
  --input-style: solid;
  --display-h1-decoration: none;
}

* {
  box-sizing: border-box;
  text-rendering: geometricprecision;
}

::selection {
  background: var(--primary-color);
  color: var(--invert-font-color);
}

body {
  font-size: var(--global-font-size);
  color: var(--font-color);
  line-height: var(--global-line-height);
  font-family: var(--font-stack);
  word-wrap: break-word;
  background-color: var(--background-color);
  margin: 0;
}

.logo, h1, h2, h3, h4, h5, h6 {
  line-height: var(--global-line-height);
}

a {
  cursor: pointer;
  color: var(--primary-color);
  text-decoration: none;
}

a:hover {
  background-color: var(--primary-color);
  color: var(--invert-font-color);
}

em {
  font-size: var(--global-font-size);
  font-style: italic;
  font-family: var(--font-stack);
  color: var(--font-color);
}

blockquote, code, em, strong {
  line-height: var(--global-line-height);
}

.logo, blockquote, code, footer, h1, h2, h3, h4, h5, h6, header, li, ol, p, section, ul {
  float: none;
  margin: 0;
  padding: 0;
}

.logo, blockquote, h1, ol, p, ul {
  margin-top: calc(var(--global-space) * 2);
  margin-bottom: calc(var(--global-space) * 2);
}

.logo, h1 {
  padding: calc(var(--global-space) * 2) 0 calc(var(--global-space) * 2);
  margin: 0;
  font-weight: 600;
  display: table-cell;
  position: relative;
  overflow: hidden;
}

h1:after {
  content: "====================================================================================================";
  display: var(--display-h1-decoration);
  position: absolute;
  bottom: 5px;
  left: 0;
}

.logo + *, h1 + * {
  margin-top: 0;
}

h2, h3, h4, h5, h6 {
  margin-bottom: var(--global-line-height);
  font-weight: 600;
  position: relative;
}

blockquote {
  padding-left: calc(var(--global-space) * 2);
  padding-left: 2ch;
  position: relative;
  overflow: hidden;
}

blockquote:after {
  content: ">\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >\a >";
  white-space: pre;
  line-height: var(--global-line-height);
  color: #9ca2ab;
  position: absolute;
  top: 0;
  left: 0;
}

code {
  font-weight: inherit;
  background-color: var(--code-bg-color);
  font-family: var(--mono-font-stack);
}

code:after, code:before {
  content: "`";
  display: inline;
}

pre code:after, pre code:before {
  content: "";
}

pre {
  word-break: break-all;
  word-wrap: break-word;
  color: var(--secondary-color);
  background-color: var(--background-color);
  border: 1px solid var(--secondary-color);
  padding: var(--global-space);
  white-space: pre-wrap;
  white-space: -moz-pre-wrap;
  white-space: -pre-wrap;
  white-space: -o-pre-wrap;
  display: block;
}

pre code {
  font-family: var(--mono-font-stack);
  min-width: 100%;
  margin: 0;
  padding: 0;
  display: inline-block;
  overflow-x: scroll;
}

.terminal .logo, .terminal blockquote, .terminal code, .terminal h1, .terminal h2, .terminal h3, .terminal h4, .terminal h5, .terminal h6, .terminal strong {
  font-size: var(--global-font-size);
  font-style: normal;
  font-family: var(--font-stack);
  color: var(--font-color);
}

.terminal-prompt {
  white-space: nowrap;
  position: relative;
}

.terminal-prompt:before {
  content: "> ";
}

.terminal-prompt:after {
  content: "";
  background: var(--primary-color);
  border-radius: 0;
  width: 3px;
  height: 1em;
  margin-left: .2em;
  animation: .8s infinite cursor;
  display: inline-block;
  position: relative;
  bottom: -2px;
}

@keyframes cursor {
  0% {
    opacity: 0;
  }

  50% {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}

li, li > ul > li {
  padding-left: calc(var(--global-space) * 2);
  display: block;
  position: relative;
}

nav > ul > li {
  padding-left: 0;
}

li:after {
  position: absolute;
  top: 0;
  left: 0;
}

ul > li:after {
  content: "-";
}

nav ul > li:after {
  content: "";
}

ol li:before {
  content: counters(item, ".") ". ";
  counter-increment: item;
}

ol ol li:before {
  content: counters(item, ".") " ";
  counter-increment: item;
}

.terminal-menu li:after, .terminal-menu li:before {
  display: none;
}

ol {
  counter-reset: item;
}

ol li:nth-child(n+10):after {
  left: -7px;
}

ol ol {
  margin-top: 0;
  margin-bottom: 0;
}

.terminal-menu {
  width: 100%;
}

.terminal-nav {
  flex-direction: column;
  align-items: flex-start;
  display: flex;
}

ul ul {
  margin-top: 0;
  margin-bottom: 0;
}

.terminal-menu ul {
  font-size: var(--global-font-size);
  flex-direction: column;
  flex-grow: 1;
  width: 100%;
  margin-top: 0;
  list-style-type: none;
  display: flex;
  padding: 0 !important;
}

.terminal-menu li {
  margin: 0 0 .5em;
  padding: 0;
  display: flex;
}

ol.terminal-toc li {
  border-bottom: 1px dotted var(--secondary-color);
  margin-bottom: 15px;
  padding: 0;
}

.terminal-menu li:last-child {
  margin-bottom: 0;
}

ol.terminal-toc li a {
  background: var(--background-color);
  text-align: left;
  margin: 4px 4px 4px 0;
  padding-right: 4px;
  position: relative;
  top: 6px;
}

.terminal-menu li a:not(.btn) {
  color: var(--secondary-color);
  border: none;
  width: 100%;
  text-decoration: none;
  display: block;
}

.terminal-menu li a.active {
  color: var(--font-color);
}

.terminal-menu li a:hover {
  color: inherit;
  background: none;
}

ol.terminal-toc li:before {
  content: counters(item, ".") ". ";
  counter-increment: item;
  background: var(--background-color);
  padding: 4px 0 4px 4px;
  position: absolute;
  bottom: -8px;
  right: 0;
}

ol.terminal-toc li a:hover {
  background: var(--primary-color);
  color: var(--invert-font-color);
}

hr {
  margin: calc(var(--global-space) * 4) 0;
  border: 0;
  border-bottom: 1px dashed var(--secondary-color);
  position: relative;
  overflow: hidden;
}

p {
  margin: 0 0 var(--global-line-height);
  color: var(--global-font-color);
}

.container {
  max-width: var(--page-width);
}

.container, .container-fluid {
  padding: 0 calc(var(--global-space) * 2);
  margin: 0 auto;
}

img {
  width: 100%;
}

.progress-bar {
  background-color: var(--progress-bar-background);
  height: 8px;
  margin: 12px 0;
}

.progress-bar.progress-bar-show-percent {
  margin-top: 38px;
}

.progress-bar-filled {
  background-color: var(--progress-bar-fill);
  width: 0;
  height: 100%;
  transition: width .3s;
  position: relative;
}

.progress-bar-filled:before {
  content: "";
  border: 6px solid #0000;
  border-top-color: var(--progress-bar-fill);
  position: absolute;
  top: -12px;
  right: -6px;
}

.progress-bar-filled:after {
  color: var(--progress-bar-fill);
  content: attr(data-filled);
  white-space: nowrap;
  border: 6px solid #0000;
  font-size: 12px;
  display: block;
  position: absolute;
  top: -38px;
  right: 0;
  transform: translateX(50%);
}

.progress-bar-no-arrow > .progress-bar-filled:after, .progress-bar-no-arrow > .progress-bar-filled:before {
  content: "";
  visibility: hidden;
  opacity: 0;
  display: none;
}

table {
  border-collapse: collapse;
  margin: var(--global-line-height) 0;
  color: var(--font-color);
  font-size: var(--global-font-size);
  width: 100%;
}

table td, table th {
  vertical-align: top;
  border: 1px solid var(--font-color);
  line-height: var(--global-line-height);
  padding: calc(var(--global-space) / 2);
  font-size: 1em;
}

table thead th {
  font-size: 1em;
}

table tfoot tr th {
  font-weight: 500;
}

table caption {
  margin: 0 0 1em;
  font-size: 1em;
}

table tbody td:first-child {
  color: var(--secondary-color);
  font-weight: 700;
}

.form {
  width: 100%;
}

fieldset {
  border: 1px solid var(--font-color);
  padding: 1em;
}

label {
  color: var(--font-color);
  font-size: 1em;
}

input[type="email"], input[type="number"], input[type="password"], input[type="search"], input[type="text"] {
  border: 1px var(--input-style) var(--font-color);
  font-size: 1em;
  font-family: var(--font-stack);
  -webkit-appearance: none;
  border-radius: 0;
  width: 100%;
  padding: .7em .5em;
}

input[type="email"]:active, input[type="email"]:focus, input[type="number"]:active, input[type="number"]:focus, input[type="password"]:active, input[type="password"]:focus, input[type="search"]:active, input[type="search"]:focus, input[type="text"]:active, input[type="text"]:focus {
  -webkit-appearance: none;
  border: 1px solid var(--font-color);
  outline: 0;
}

input[type="email"]:not(:placeholder-shown):invalid, input[type="number"]:not(:placeholder-shown):invalid, input[type="password"]:not(:placeholder-shown):invalid, input[type="search"]:not(:placeholder-shown):invalid, input[type="text"]:not(:placeholder-shown):invalid {
  border-color: var(--error-color);
}

input, textarea {
  color: var(--font-color);
  background-color: var(--background-color);
}

input::-webkit-input-placeholder {
  opacity: 1;
  color: var(--secondary-color) !important;
}

textarea::-webkit-input-placeholder {
  opacity: 1;
  color: var(--secondary-color) !important;
}

input::-moz-placeholder {
  opacity: 1;
  color: var(--secondary-color) !important;
}

textarea::-moz-placeholder {
  opacity: 1;
  color: var(--secondary-color) !important;
}

:is(input:-ms-input-placeholder, textarea:-ms-input-placeholder) {
  opacity: 1;
  color: var(--secondary-color) !important;
}

input::-moz-placeholder {
  opacity: 1;
  color: var(--secondary-color) !important;
}

textarea::-moz-placeholder {
  opacity: 1;
  color: var(--secondary-color) !important;
}

input::placeholder, textarea::placeholder {
  opacity: 1;
  color: var(--secondary-color) !important;
}

textarea {
  resize: none;
  border: 1px var(--input-style) var(--font-color);
  font-size: 1em;
  font-family: var(--font-stack);
  -webkit-appearance: none;
  border-radius: 0;
  width: 100%;
  height: auto;
  padding: .5em;
}

textarea:focus {
  -webkit-appearance: none;
  border: 1px solid var(--font-color);
  outline: 0;
}

textarea:not(:placeholder-shown):invalid {
  border-color: var(--error-color);
}

input:-webkit-autofill {
  border: 1px solid var(--font-color);
  -webkit-text-fill-color: var(--font-color);
  box-shadow: 0 0 0 1000px var(--invert-font-color) inset;
  -webkit-box-shadow: 0 0 0 1000px var(--invert-font-color) inset;
  transition: background-color 5000s ease-in-out;
}

input:-webkit-autofill:focus textarea:-webkit-autofill {
  border: 1px solid var(--font-color);
  -webkit-text-fill-color: var(--font-color);
  box-shadow: 0 0 0 1000px var(--invert-font-color) inset;
  -webkit-box-shadow: 0 0 0 1000px var(--invert-font-color) inset;
  transition: background-color 5000s ease-in-out;
}

input:-webkit-autofill:hover {
  border: 1px solid var(--font-color);
  -webkit-text-fill-color: var(--font-color);
  box-shadow: 0 0 0 1000px var(--invert-font-color) inset;
  -webkit-box-shadow: 0 0 0 1000px var(--invert-font-color) inset;
  transition: background-color 5000s ease-in-out;
}

select:-webkit-autofill {
  border: 1px solid var(--font-color);
  -webkit-text-fill-color: var(--font-color);
  box-shadow: 0 0 0 1000px var(--invert-font-color) inset;
  -webkit-box-shadow: 0 0 0 1000px var(--invert-font-color) inset;
  transition: background-color 5000s ease-in-out;
}

select:-webkit-autofill:focus {
  border: 1px solid var(--font-color);
  -webkit-text-fill-color: var(--font-color);
  box-shadow: 0 0 0 1000px var(--invert-font-color) inset;
  -webkit-box-shadow: 0 0 0 1000px var(--invert-font-color) inset;
  transition: background-color 5000s ease-in-out;
}

select:-webkit-autofill:hover {
  border: 1px solid var(--font-color);
  -webkit-text-fill-color: var(--font-color);
  box-shadow: 0 0 0 1000px var(--invert-font-color) inset;
  -webkit-box-shadow: 0 0 0 1000px var(--invert-font-color) inset;
  transition: background-color 5000s ease-in-out;
}

textarea:-webkit-autofill:hover textarea:-webkit-autofill:focus {
  border: 1px solid var(--font-color);
  -webkit-text-fill-color: var(--font-color);
  box-shadow: 0 0 0 1000px var(--invert-font-color) inset;
  -webkit-box-shadow: 0 0 0 1000px var(--invert-font-color) inset;
  transition: background-color 5000s ease-in-out;
}

.form-group {
  margin-bottom: var(--global-line-height);
  overflow: auto;
}

.btn {
  cursor: pointer;
  -webkit-user-select: none;
  user-select: none;
  z-index: 1;
  border-style: solid;
  border-width: 1px;
  outline: 0;
  justify-content: center;
  align-items: center;
  padding: .65em 2em;
  font-family: inherit;
  font-size: 1em;
  display: inline-flex;
  position: relative;
}

.btn:active {
  box-shadow: none;
}

.btn.btn-ghost {
  border-color: var(--font-color);
  color: var(--font-color);
  background-color: #0000;
}

.btn.btn-ghost:focus, .btn.btn-ghost:hover {
  border-color: var(--tertiary-color);
  color: var(--tertiary-color);
  z-index: 2;
}

.btn.btn-ghost:hover {
  background-color: #0000;
}

.btn-block {
  width: 100%;
  display: flex;
}

.btn-default {
  background-color: var(--font-color);
  border-color: var(--invert-font-color);
  color: var(--invert-font-color);
}

.btn-default:focus:not(.btn-ghost), .btn-default:hover {
  background-color: var(--secondary-color);
  color: var(--invert-font-color);
}

.btn-default.btn-ghost:focus, .btn-default.btn-ghost:hover {
  border-color: var(--secondary-color);
  color: var(--secondary-color);
  z-index: 2;
}

.btn-error {
  color: var(--invert-font-color);
  background-color: var(--error-color);
  border: 1px solid var(--error-color);
}

.btn-error:focus:not(.btn-ghost), .btn-error:hover {
  background-color: var(--error-color);
  border-color: var(--error-color);
}

.btn-error.btn-ghost {
  border-color: var(--error-color);
  color: var(--error-color);
}

.btn-error.btn-ghost:focus, .btn-error.btn-ghost:hover {
  border-color: var(--error-color);
  color: var(--error-color);
  z-index: 2;
}

.btn-primary {
  color: var(--invert-font-color);
  background-color: var(--primary-color);
  border: 1px solid var(--primary-color);
}

.btn-primary:focus:not(.btn-ghost), .btn-primary:hover {
  background-color: var(--primary-color);
  border-color: var(--primary-color);
}

.btn-primary.btn-ghost {
  border-color: var(--primary-color);
  color: var(--primary-color);
}

.btn-primary.btn-ghost:focus, .btn-primary.btn-ghost:hover {
  border-color: var(--primary-color);
  color: var(--primary-color);
  z-index: 2;
}

.btn-small {
  padding: .5em 1.3em !important;
  font-size: .9em !important;
}

.btn-group {
  overflow: auto;
}

.btn-group .btn {
  float: left;
}

.btn-group .btn-ghost:not(:first-child) {
  margin-left: -1px;
}

.terminal-card {
  border: 1px solid var(--secondary-color);
}

.terminal-card > header {
  color: var(--invert-font-color);
  text-align: center;
  background-color: var(--secondary-color);
  padding: .5em 0;
}

.terminal-card > div:first-of-type {
  padding: var(--global-space);
}

.terminal-timeline {
  padding-left: 70px;
  position: relative;
}

.terminal-timeline:before {
  content: " ";
  background: var(--secondary-color);
  z-index: 400;
  width: 2px;
  height: 100%;
  display: inline-block;
  position: absolute;
  left: 35px;
}

.terminal-timeline .terminal-card {
  margin-bottom: 25px;
}

.terminal-timeline .terminal-card:before {
  content: " ";
  background: var(--invert-font-color);
  border: 2px solid var(--secondary-color);
  z-index: 400;
  width: 15px;
  height: 15px;
  margin-top: 25px;
  display: inline-block;
  position: absolute;
  left: 26px;
}

.terminal-alert {
  color: var(--font-color);
  border: 1px solid var(--font-color);
  margin-bottom: var(--global-space);
  padding: 1em;
}

.terminal-alert-error {
  color: var(--error-color);
  border-color: var(--error-color);
}

.terminal-alert-primary {
  color: var(--primary-color);
  border-color: var(--primary-color);
}

@media screen and (width <= 960px) {
  label {
    width: 100%;
    display: block;
  }

  pre::-webkit-scrollbar {
    height: 3px;
  }
}

@media screen and (width <= 480px) {
  form {
    width: 100%;
  }
}

@media only screen and (width >= 30em) {
  .terminal-nav {
    flex-direction: row;
    align-items: center;
  }

  .terminal-menu ul {
    margin-top: calc(var(--global-space) * 2);
    flex-direction: row;
    justify-content: flex-end;
    place-items: center flex-end;
  }

  .terminal-menu li {
    margin: 0 2em 0 0;
  }

  .terminal-menu li:last-child {
    margin-right: 0;
  }
}

.terminal-media:not(:last-child) {
  margin-bottom: 1.25rem;
}

.terminal-media-left {
  padding-right: var(--global-space);
}

.terminal-media-left, .terminal-media-right {
  vertical-align: top;
  display: table-cell;
}

.terminal-media-right {
  padding-left: var(--global-space);
}

.terminal-media-body {
  vertical-align: top;
  display: table-cell;
}

.terminal-media-heading {
  font-size: 1em;
  font-weight: 700;
}

.terminal-media-content {
  margin-top: .3rem;
}

.terminal-placeholder {
  background-color: var(--secondary-color);
  text-align: center;
  color: var(--font-color);
  border: 1px solid var(--secondary-color);
  font-size: 1rem;
}

figure > img {
  padding: 0;
}

.terminal-avatarholder {
  width: calc(var(--global-space) * 5);
  height: calc(var(--global-space) * 5);
}

.terminal-avatarholder img {
  padding: 0;
}

figure {
  margin: 0;
}

figure > figcaption {
  color: var(--secondary-color);
  text-align: center;
}

.hljs {
  background: var(--block-background-color);
  color: var(--font-color);
  padding: .5em;
  display: block;
  overflow-x: auto;
}

.hljs-comment, .hljs-quote {
  color: var(--secondary-color);
}

.hljs-variable {
  color: var(--font-color);
}

.hljs-built_in, .hljs-keyword, .hljs-name, .hljs-selector-tag, .hljs-tag {
  color: var(--primary-color);
}

.hljs-addition, .hljs-attribute, .hljs-literal, .hljs-section, .hljs-string, .hljs-template-tag, .hljs-template-variable, .hljs-title, .hljs-type, .hljs-string {
  color: var(--secondary-color);
}

.hljs-deletion, .hljs-meta, .hljs-selector-attr, .hljs-selector-pseudo {
