}

:root {
  --gb-dm-bg0: #282828;
  --gb-dm-bg0-hard: #1d2021;
  --gb-dm-bg0-soft: #32302f;
  --gb-dm-bg1: #3c3836;
  --gb-dm-bg2: #504945;
